import { dateInterface } from '../../utils/swift-calendar/types';
import {
  daysInCalendarWithFiveRows,
  daysInCalendarWithFourRows,
  daysInCalendarWithThreeRows,
  notInThisMonthCounts,
} from '../../utils/swift-calendar/constants';

export class Calendar {
  readonly year: number;
  readonly month: number;

  public daysInCalendar = daysInCalendarWithFourRows;

  private notInThisMonthStartCount = 0;
  private notInThisMonthEndCount = 0;
  private currentMonthCount: number;
  private notInThisMonthCounts = notInThisMonthCounts;

  constructor(year: number, month: number) {
    this.year = year;
    this.month = month;
  }

  public getCalendarDays(): number[] {
    const thisMonthDays = this.getCurrentMonthDays();
    const notInThisMonthStartCount = this.notInThisMonthCounts[this.getMonthFirstDay()];
    const notInThisMonthEndCount = daysInCalendarWithFourRows - (thisMonthDays.length + notInThisMonthStartCount);

    this.currentMonthCount = thisMonthDays.length;
    this.notInThisMonthStartCount = notInThisMonthStartCount;
    this.notInThisMonthEndCount = notInThisMonthEndCount;

    const fillStart: number[] = notInThisMonthStartCount > 0 ? this.getDaysOfLastMonth(notInThisMonthStartCount).map(_ => 0) : [];
    const fillEnd: number[] = this.getNextMonthDays(notInThisMonthEndCount).map(_ => 0);

    return [...fillStart, ...thisMonthDays, ...fillEnd];
  }

  private getCurrentMonthDays(): number[] {
    return this.getDaysOfMonth(this.month);
  }

  private getDaysOfLastMonth(notInThisMonthStartCount: number): number[] {
    const daysOfMonth = this.getDaysOfMonth(this.month - 1);

    return daysOfMonth.slice(-notInThisMonthStartCount);
  }

  private getNextMonthDays(endCount: number): number[] {
    const { currentMonthCount, notInThisMonthStartCount } = this;
    const daysOfMonth = this.getDaysOfMonth(this.month + 1);
    const currentMonthWithStartCount = currentMonthCount + notInThisMonthStartCount;

    if (endCount <= -1) {
      endCount = daysInCalendarWithFiveRows - currentMonthWithStartCount;
      this.daysInCalendar = daysInCalendarWithFiveRows;
    } else if (endCount === 7 && (currentMonthWithStartCount) === 28) {
      endCount = daysInCalendarWithThreeRows - currentMonthWithStartCount;
      this.daysInCalendar = daysInCalendarWithThreeRows;
    }

    this.notInThisMonthEndCount = endCount;

    return daysOfMonth.slice(0, endCount);
  }

  private getDaysOfMonth(month: number): number[] {
    const daysOfMonth = new Date(this.year, month, 0).getDate();

    return Array.from({length: daysOfMonth}, (_, i) => i + 1);
  }

  public getMonthFirstDay(): number {
    return new Date(this.year, this.month - 1, 1).getDay();
  }

  public getNotInThisMonthStartCount(): number {
    return this.notInThisMonthStartCount;
  }

  public getNotInThisMonthEndCount(): number {
    return this.notInThisMonthEndCount;
  }

  public static getToday(): dateInterface {
    const now = new Date();

    return {
      date: `${now.getFullYear()}-${now.getMonth() + 1}-${now.getDate()}`,
      month: now.getMonth() + 1,
      day: now.getDate(),
      year: now.getFullYear(),
    };
  }
}
