import { Component, Prop, h, State, Fragment, getAssetPath } from '@stencil/core';
import { Calendar } from '../../utils/swift-calendar/';
import { dateInterface, Direction, eventInterface } from '../../utils/swift-calendar/types';
import { days, months } from '../../utils/swift-calendar/constants';
import { yamlToJsDataFromWeb } from '../../utils';

@Component({
  tag: 'swift-calendar',
  styleUrl: 'swift-calendar.scss',
  scoped: true,
})
export class SwiftCalendar {
  @Prop()
  theme?: string = 'auto';
  @Prop()
  jsData?: string;
  @Prop()
  pathDataFile?: string;
  @Prop()
  pathIcalFile?: string;

  @State()
  todayDate = Calendar.getToday();
  @State()
  daysInMonth: number[];
  @State()
  selectedDate: dateInterface|null;
  @State()
  isModalOpen = false;
  @State()
  modalData: eventInterface;
  @State()
  events: eventInterface[] = [];
  @State()
  isEventsView = false;

  readonly today: dateInterface;

  constructor() {
    this.today = Calendar.getToday();
    this.switchMonth = this.switchMonth.bind(this);
    this.onDayClick = this.onDayClick.bind(this);
    this.switchView = this.switchView.bind(this);
  }

  async componentWillLoad(): Promise<void> {
    let doc: eventInterface[];

    if (this.jsData) {
      doc = JSON.parse(this.jsData);
    } else if (this.pathDataFile) {
      doc = await yamlToJsDataFromWeb(this.pathDataFile);
    }

    if (doc && doc.length > 0) {
      this.events = doc;
    }

    this.setupCalendar();
  }

  setupCalendar(): void {
    const date = this.todayDate;
    const calendar = new Calendar(date.year, date.month);

    this.daysInMonth = calendar.getCalendarDays();
  }

  daySelectedHandler = (day: any): void => {
    this.selectedDate = {
      date: `${this.todayDate.year}-${this.todayDate.month}-${day}`,
      month: this.todayDate.month,
      day,
      year: this.todayDate.year
    };
  }

  openDayDetails = (day: any): void => {
    const events = this.getEvents(day);

    this.modalData = {...this.modalData, ...events};
    this.isModalOpen = true;
  }

  closeDayDetails = (): any => {
    this.isModalOpen = false;
    this.selectedDate = null;
  }

  switchMonth(event: Event): void {
    const target = event.currentTarget as HTMLButtonElement;
    const nextOrPrev: Direction = target.getAttribute('data-direction') === '1'
      ? Direction.Previous
      : Direction.Next

    if (nextOrPrev === Direction.Previous) {
      if (this.todayDate.month !== 1) {
        this.todayDate.month -= 1;
      } else {
        this.todayDate.month = 12;
        this.todayDate.year -= 1;
      }
    } else {
      if (this.todayDate.month !== 12) {
        this.todayDate.month += 1;
      } else {
        this.todayDate.month = 1;
        this.todayDate.year += 1;
      }
    }

    delete this.todayDate.day;
    this.setupCalendar();
  }

  switchView(event: Event): void {
    const target = event.currentTarget as HTMLInputElement;

    this.isEventsView = target.checked;
  }

  getEvents = (day: number): eventInterface => {

    return this.events.find(event => {
      const dateArray: string[] = event.date.split('-');

      return parseInt(dateArray[1]) === this.todayDate.month && parseInt(dateArray[2]) === day && parseInt(dateArray[0]) === this.todayDate.year
    });
  }

  hasEvents = (day: number): boolean => {
    const events = this.getEvents(day);

    return events?.entries?.length > 0;
  }

  getDayClasses = (month: number, day: number, year: number): string => {
    const dayClasses = [];

    if (this.isToday(month, day, year)) {
      dayClasses.push('active');
    }

    if (this.isSelectedDay(day)) {
      dayClasses.push('selected');
    }

    if (this.hasEvents(day)) {
      dayClasses.push('has-events');
    }

    return dayClasses.join(' ');
  }

  isToday(month: number, day: number, year: number): boolean {
    const { today } = this;

    return today.month === month && today.day === day && today.year === year
  }

  isSelectedDay(day: number): boolean {
    const { selectedDate, todayDate } = this;

    return (selectedDate && Object.keys(selectedDate).length > 0)
      && selectedDate.month === todayDate.month
      && selectedDate.day === day
      && selectedDate.year === todayDate.year
  }

  onDayClick(event: Event): void {
    const target = event.currentTarget as HTMLButtonElement;

    if (target.classList.contains('day-details') || target.firstElementChild.classList.contains('has-events')) {
      const day = +target.getAttribute('data-day');

      this.daySelectedHandler(day);
      this.openDayDetails(day);
    }
  }

  getMenu(date: dateInterface, theme: string): Element {
    return (
      <div class="menu">
        <button onClick={this.switchMonth} data-direction={ Direction.Previous }>
          <img class="nav-button" src={getAssetPath(`./components/assets/prev-${theme}.png`)} />
        </button>
        <span>{months[date.month - 1]} {date.year}</span>
        <button onClick={this.switchMonth} data-direction={ Direction.Next }>
          <img class="nav-button" src={getAssetPath(`./components/assets/next-${theme}.png`)} />
        </button>
      </div>
    )
  }

  getActions(): Element {
    return (
      <ul class="actions">
        <li>
          <input
            type="checkbox"
            id="events-view"
            name="events-view"
            checked={this.isEventsView}
            onChange={this.switchView}
          />
          <label htmlFor="events-view"> Events view</label>
        </li>
      </ul>
    )
  }

  getDays(): Element {
    return (
      <div class="days">
        {days.map(dayName => <span>{dayName}</span>)}
      </div>
    )
  }

  getDayCells(date: dateInterface): Element {
    return (
      <div class="day-numbers">
        {this.daysInMonth.map(day => {
          if (day === 0) {
            return (
              <span class="no-pointer"></span>
            );
          }

          return (
            <span onClick={this.onDayClick} role='button' data-day={ day }>
              <i class={this.getDayClasses(date.month, day, date.year)}>
                {day}
              </i>
            </span>
          );
        })}
      </div>
    )
  }

  getShortEventList(day: number): Element {
    return (
      <Fragment>
        {this.getEvents(day).entries.map(event =>
          <div class="event-name">
            {event.name}
          </div>
        )}
      </Fragment>
    )
  }

  getDayWithEventsList(date: dateInterface): Element {
    return (
      <div class="event-list">
        {this.daysInMonth.map(day => {
          if (day !== 0 && this.hasEvents(day)) {
            return (
              <div class="day-details" role='button' onClick={this.onDayClick} data-day={ day }>
                <div class="day">
                  {`${date.month}/${day}/${date.year}`}
                </div>
                <div class="events">
                  {this.getShortEventList(day)}
                </div>
              </div>
            )
          }
        })}
      </div>
    )
  }

  getModalContent(): Element {
    const { date, entries } = this.modalData;
    const dateArray = date.split('-');

    return (
      <Fragment>
        <div class="pre-entry">
          <p class="date">{dateArray[1]}/{dateArray[2]}/{dateArray[0]}</p>
          {this.pathIcalFile &&
            <p class="download">
              &nbsp;(<a href={this.pathIcalFile}>download iCal</a>)
            </p>
          }
        </div>
        {entries.map(entry =>
          <div class="entry">
            <div class="details">
              <div>
                <p class="name">{entry.name}</p>
                <p class="description">{entry.description}</p>
              </div>
              <div>
                <p class="location">{entry.location}</p>
                <p class="url"><a href={entry.url} target="_blank">{entry.url}</a></p>
              </div>
            </div>
          </div>
        )}
      </Fragment>
    )
  }

  render(): Element {
    const theme = this.theme === 'auto' ? 'light' : this.theme;

    return (
      <div class={`swift-calendar theme-${theme}`}>
        <swift-modal
          theme={this.theme}
          show={this.isModalOpen}
          close={this.closeDayDetails}
        >
          {this.isModalOpen
            ? this.getModalContent()
            : null
          }
        </swift-modal>

        {this.getMenu(this.todayDate, theme)}
        {this.getActions()}

        {this.isEventsView
          ?
            <Fragment>
              {this.getDayWithEventsList(this.todayDate)}
            </Fragment>
          :
            <Fragment>
              {this.getDays()}
              {this.getDayCells(this.todayDate)}
            </Fragment>
        }
      </div>
    );
  }
}
