import { Component, Prop, getAssetPath, h } from '@stencil/core';

@Component({
  tag: 'swift-modal',
  styleUrl: 'swift-modal.scss',
  scoped: true,
  assetsDirs: ['assets'],
})
export class SwiftModal {
  @Prop()
  theme?: string = 'auto';
  @Prop({
    mutable: true,
    reflect: true,
  })
  show = false;
  @Prop()
  close: () => void;

  constructor() {
    this.hide = this.hide.bind(this);
  }

  hide(): void {
    this.show = false;
    this.close();
  }

  render() {
    const theme = this.theme === 'auto' ? 'light' : this.theme;

    return (
      <div class={this.show ? `swift-modal theme-${theme} show` : `swift-modal`}>
        <div class="content">
          <div class="header">
            <button onClick={this.hide}>
              <img class="close-button" src={getAssetPath(`./components/assets/close-${theme}.png`)} />
            </button>
          </div>
          <div class="body">
            <slot />
          </div>
        </div>
      </div>
    );
  }
}
