import yaml from 'js-yaml';
import { eventInterface } from './swift-calendar/types';

export const yamlToJsDataFromWeb = async (fileUrl: string = '') => {
  if (fileUrl === '') {
    return;
  }

  const response = await fetch(fileUrl);

  if (response.status === 200) {
    const blob = await response.blob();
    const text = await blob.text();
    const jsData: eventInterface[]|{} = yaml.load(text);

    if (Array.isArray(jsData)) {
      return jsData;
    }
  }
}
